import React from 'react'
import { Button } from 'react-bootstrap'
import { ProductosContext, ProductosType } from '../../../Context/Producto'
import { Detalles } from '../../../Modelos/General'
import { Producto } from '../../../Modelos/Productos'
import THFila from '../../../Utiles/Componentes/THFila'
import { InputNumber } from '../../../Utiles/Inputs'

interface Props {
    producto: Producto,
    add: (iten: Detalles) => void
}
class FilaCotizacion extends React.Component<Props, Detalles>{
    constructor(p: Props) {
        super(p)
        let { costo, precio2 } = p.producto;
        this.state = {
            ...p.producto,
            unidades: 1,
            comentario: '',
            precio: parseFloat((costo * (1 + precio2 / 100)).toFixed(2))
        }
        this.setState = this.setState.bind(this)
    }
    render() {
        const { data: { User: { nivel } }, findProducto } = this.context as ProductosType;
        const { unidades, precio, codigo } = this.state;
        const { costo, precio1, precio2, precio3, descripcion } = findProducto(codigo);
        let p = (nivel === 0) ? costo.toFixed(2) : (costo * (1 + precio1 / 100)).toFixed(2);
        return <tr>
            <THFila width='90px'>{codigo}</THFila>
            <THFila width='25%'>{descripcion}</THFila>
            <td><InputNumber min={1} setData={this.setState} value={unidades} update="unidades" /></td>
            <td>
                <InputNumber step='0.01' list={costo + 'list'} min={parseFloat(p)}
                    setData={this.setState} value={precio} update="precio" />
                <datalist id={costo + 'list'}>
                    <option>{(costo * (1 + precio1 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio2 / 100)).toFixed(2)}</option>
                    <option>{(costo * (1 + precio3 / 100)).toFixed(2)}</option>
                </datalist>
            </td>
            <td><InputNumber disabled setData={() => { }} value={precio * unidades} update="p" /></td>
            <td style={{ textAlign: 'right' }}>
                <Button data-tip="AGREGAR PRODUCTO" onClick={() => { this.props.add(this.state) }} variant="primary">
                    <i className="fa fa-plus" />
                </Button>
            </td>
        </tr>
    }
}
FilaCotizacion.contextType = ProductosContext;
export default FilaCotizacion;